body {
  min-width: 320px;
  font-family: "Roboto Condensed", "Bebas Neue", "Arial", serif;
  color: #103aae;
  background-color: #ffffff;
  font-size: 16px;
  margin: 0;
  padding: 0;
  text-align: left; }

a {
  text-decoration: none;
  color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  color: #ffffff; }
  @media (max-width: 1199px) {
    .header {
      min-height: 554px; } }
  @media (max-width: 767px) {
    .header {
      background: conic-gradient(from 171.43deg at 35.31% 34.22%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg); } }

.header__wrapper {
  margin: 0 auto;
  max-width: 1160px;
  padding-bottom: 50px; }
  @media (max-width: 1199px) {
    .header__wrapper {
      padding: 0 6%; } }
  @media (max-width: 767px) {
    .header__wrapper {
      padding: 5%; } }

.header__top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 30px; }
  .header__top a {
    font-size: 26px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.03em; }
    @media (max-width: 1199px) {
      .header__top a {
        font-size: 24px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0.02em; } }
    @media (max-width: 767px) {
      .header__top a {
        font-size: 20px;
        font-weight: 400;
        line-height: 20px; } }
  @media (max-width: 1199px) {
    .header__top {
      padding-top: 40px; } }

.header__main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.header__social-list {
  list-style: none;
  margin: 0;
  padding: 0;
  align-self: flex-end; }
  .header__social-list li {
    margin-bottom: 24px; }
  @media (max-width: 1199px) {
    .header__social-list {
      display: none; } }

.header__main-wrapper {
  width: 80%;
  background-image: url("../img/banner-photo-desktop@1x.png");
  background-repeat: no-repeat;
  background-position: top right;
  min-height: 556px; }
  @media (max-width: 1199px) {
    .header__main-wrapper {
      background-image: url("../img/banner-photo-tablet@1x.png");
      width: 100%;
      margin: 25px 0;
      min-height: 390px; } }
  @media (max-width: 767px) {
    .header__main-wrapper {
      background-image: none; } }

.header__description {
  width: 42%;
  text-align: right; }
  .header__description h1 {
    font-size: 89px;
    font-weight: 700;
    line-height: 109px;
    margin: 0;
    text-transform: uppercase;
    margin-left: auto; }
    @media (max-width: 1199px) {
      .header__description h1 {
        font-size: 66px;
        font-weight: 700;
        line-height: 86px;
        max-width: 300px; } }
    @media (max-width: 1199px) {
      .header__description h1 {
        font-size: 60px;
        font-weight: 700;
        line-height: 78px; } }
  .header__description ul {
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    position: relative;
    top: -90px;
    right: 280px; }
    @media (max-width: 1199px) {
      .header__description ul {
        top: -80px;
        right: 210px; } }
    @media (max-width: 1199px) {
      .header__description ul {
        top: -70px;
        right: 180px; } }
  .header__description p {
    font-size: 49px;
    font-weight: 400;
    line-height: 58px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    margin-bottom: 0; }
    @media (max-width: 1199px) {
      .header__description p {
        font-size: 34px;
        font-weight: 400;
        line-height: 41px; } }
    @media (max-width: 767px) {
      .header__description p {
        font-size: 28px;
        font-weight: 400;
        line-height: 34px; } }
  .header__description a {
    padding: 24px 56px; }
    @media (max-width: 1199px) {
      .header__description a {
        padding: 24px 33px; } }
    @media (max-width: 767px) {
      .header__description a {
        width: 100%; } }
  @media (max-width: 1199px) {
    .header__description {
      width: 48%; } }
  @media (max-width: 767px) {
    .header__description {
      width: 100%; } }

.button {
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  letter-spacing: 0.05em;
  background-color: transparent;
  border: 4px solid #ffffff;
  text-transform: uppercase;
  cursor: pointer;
  box-sizing: border-box; }
